.contributors-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: #070632;
    padding: 50px 40px;
}

.contributors-body--title-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contributors-body--title-container > span {
    font-family: IBMPlexMono;
    font-size: 18px;
    width: 306px;
    height: 46px;
    font-weight: bold;
    font-stretch: normal;
    font-style: italic;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
}

.contributors--logo {
    margin: 0px 40px;
}

@media screen and (min-width: 600px) {
    .contributors-body {
        flex-direction: row;
        justify-content: flex-end;
    }
    .contributors-body--title-container{
        margin: 0px 20px;
        display: block;
    }
    .contributors-body--title-container > span {
        font-size: 24px;
    }
    .contributors--logo {
        width: 150px;
        margin: 0px;
    }
}